.card-temp {
  font-size: 18px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8px;
  gap: 10px;
  position: relative;
  background-color: rgb(255, 255, 255);
  border: 3px solid #333333;
  border-radius: 8px;
  margin-bottom: 8px;
}
